:root {
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


:root {
    --paletteColor1: #000;
    --paletteColor2: #fff;
    --paletteColor3:#161619;
    --paletteColor4: #E21221;
    --paletteColor5:#fad0c4;
    --paletteColor6: #ffd1ff;
    --paletteColor7: #3A1C71;
    --paletteColor8: #D76D77;
    --paletteColor9: #FFAF7B;
    --paletteColor10: #f1c40f;
    --paletteColor11: #FF7A69;
    
    --filledProgressBar: 0%;
    --backgroundFontSize: 6vw;
}

body {
    margin: 0;
    padding: 0;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    min-height: 100vh;
    background: -webkit-linear-gradient(250deg, #3A1C71, #D76D77, #FFAF7B);
    background: linear-gradient(200deg, #3A1C71, #D76D77, #FFAF7B);
    background: -webkit-linear-gradient(250deg, var(--paletteColor7), var(--paletteColor8), var(--paletteColor9));
    background: linear-gradient(200deg, var(--paletteColor7), var(--paletteColor8), var(--paletteColor9));
    background-size: cover;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
            justify-content: center
}

body::before, body::after {
        position: absolute;
        font-size: 6vw;
        font-size: var(--backgroundFontSize);
        text-transform: uppercase;
        font-weight: 900;
        color: #f1c40f;
        color: var(--paletteColor10);
        z-index: 0;
    }

body::before {
        top: 35px;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        content: '\432\438\434\435\43E\43F\43B\435\435\440';
    }

body .player {
        max-width: 750px;
        border: 2px solid rgba(0, 0, 0, 0.2);
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
        position: relative;
        overflow: hidden;
        z-index: 1
    }

body .player video {
            width: 100%;
        }

body .player .controls {
            display: -webkit-box;
            display: -webkit-flex;
            display: flex;
            position: absolute;
            bottom: 0;
            width: 100%;
            -webkit-transform: translateY(100%) translateY(-5px);
                    transform: translateY(100%) translateY(-5px);
            -webkit-transition: all 0.3s;
            transition: all 0.3s;
            -webkit-flex-wrap: wrap;
                    flex-wrap: wrap;
            background: rgba(0, 0, 0, 0.1)
        }

body .player .controls .progress {
                -webkit-box-flex: 10;
                -webkit-flex: 10;
                        flex: 10;
                position: relative;
                display: -webkit-box;
                display: -webkit-flex;
                display: flex;
                -webkit-flex-basis: 100%;
                        flex-basis: 100%;
                height: 5px;
                -webkit-transition: height 0.3s;
                transition: height 0.3s;
                background: rgba(0, 0, 0, 0.5);
                cursor: pointer
            }

body .player .controls .progress .filled {
                    width: 50%;
                    background: #E21221;
                    background: var(--paletteColor4);
                    -webkit-box-flex: 0;
                    -webkit-flex: 0;
                            flex: 0;
                    -webkit-flex-basis: 0%;
                            flex-basis: 0%;
                    -webkit-flex-basis: var(--filledProgressBar);
                            flex-basis: var(--filledProgressBar);
                }

body .player .controls .slider {
                width: 10px;
                height: 30px;
            }

body .player .controls button {
                background: none;
                border: 0;
                line-height: 1;
                color: white;
                text-align: center;
                outline: 0;
                padding: 0;
                cursor: pointer;
                max-width: 50px;
                -webkit-transition: color .3s ease-in-out;
                transition: color .3s ease-in-out
            }

body .player .controls button::-moz-selection {
                    background-color: transparent;
                    color: white;
                }

body .player .controls button::selection {
                    background-color: transparent;
                    color: white;
                }

body .player .controls button:hover {
                    color: #E21221;
                    color: var(--paletteColor4);
                }

body .player:hover .controls {
            -webkit-transform: translateY(0);
                    transform: translateY(0)
        }

body .player:hover .controls .progress {
                height: 15px;
            }

@media (max-width: 414px), (min-width: 415px) and (max-width: 667px) {

body {
        --backgroundFontSize: 42px;
        padding: 0 5px
}

        body::before {
            top: 25px;
        }
        body::after {
            bottom: 25px;
        }
    }

@media (min-width: 415px) and (max-width: 667px) {

body {
        --backgroundFontSize: 54px
}
    }

.controls > * {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
            flex: 1;
}

input[type='range'] {
        -webkit-appearance: none;
        background: transparent;
        width: 100%;
        margin: 0 5px
    }

input[type='range']:hover {
            outline: none;
            background: transparent
        }

input[type='range']:hover::-webkit-slider-runnable-track {
                -webkit-transition: background 1s ease;
                transition: background 1s ease;
                background: #FF7A69;
                background: var(--paletteColor11);
            }

input[type='range']:focus {
            outline: none;
        }

input[type='range']::-webkit-slider-runnable-track {
            width: 100%;
            height: 8.4px;
            background: rgba(255, 255, 255, 0.8);
            border-radius: 1.3px;
            border: 0.2px solid rgba(1, 1, 1, 0);
        }

input[type='range']::-webkit-slider-thumb {
            -webkit-appearance: none;
            height: 15px;
            width: 15px;
            border-radius: 50px;
            background: #E21221;
            background: var(--paletteColor4);
            cursor: pointer;
            margin-top: -3.5px;
            box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
        }

input[type='range']::-moz-range-track {
            width: 100%;
            height: 8.4px;
            cursor: pointer;
            box-shadow: 1px 1px 1px rgba(0, 0, 0, 0), 0 0 1px rgba(13, 13, 13, 0);
            background: #fff;
            background: var(--paletteColor2);
            border-radius: 1.3px;
            border: 0.2px solid rgba(1, 1, 1, 0);
        }

input[type='range']::-moz-range-thumb {
            box-shadow: 0 0 0 rgba(0, 0, 0, 0), 0 0 0 rgba(13, 13, 13, 0);
            height: 15px;
            width: 15px;
            border-radius: 50px;
            background: #E21221;
            background: var(--paletteColor4);
            cursor: pointer;
        }
